<template>
  <div id="a_wait_investor_comfirm">
    <!-- <nav>
      <van-icon name="arrow-left" @click="$global.previous()" />
    </nav>-->
    <commonnav :msg="dad_text"></commonnav>
    <main>
      <div class="investors_infor">
        <h2>{{title}}</h2>
        <header></header>
          <commoninvestors :investor_infor="investor_infor"></commoninvestors>
      </div>
      <article>
        <header></header>
        <boxx :nav_lists="nav_lists"></boxx>
        <commondetails :toson="details_lists"></commondetails>
        <footer>
          <aside>
            <p>(IPFS)</p>
            <button @click="check_contract"></button>
          </aside>
        </footer>
      </article>
    </main>
    <mbottom></mbottom>
  </div>
</template>
<script>
export default {
  name: "goods_details",
  data() {
    return {
      title: "",
      details: {},
      dad_text: "",
       investor_infor: {
        investorsType: {
          name: ":",
          response: ""
        },
        investorsCompany: {
          name: ":",
          response: ""
        },
        investorsName: {
          name: ":",
          response: ""
        },
        investorsArea: {
          name: ":",
          response: ""
        }
      },
      nav_lists: {
        financingStage: {
          name: "",
          response: ""
        },
        committedCount: {
          name: "</br>",
          response: ""
        },
        interestProjectCount: {
          name: "<br>",
          response: ""
        },

      },
      details_lists: {
        projectIndustry: {
          name: ":",
          response: ""
        },
        projectArea: {
          name: ":",
          response: ""
        },
        // signStatu: {
        //   name: ":",
        //   response: ""
        // },
        projectCompany: {
          name: ":",
          response: ""
        },
        currencyType: {
          name: ":",
          response: ""
        },
        collectMoney: {
          name: ":",
          collectMoneyMin:'',
          collectMoneyMax: "",

        },
        projectMobile: {
          name: ":",
          response: ""
        },
        projectEmail: {
          name: ":",
          response: ""
        },
        projectDescribe: {
          name: ":",
          response: ""
        }
      }
    };
  },
  created() {
    this.details = this.$route.query;
    this.$loading();
    this.$global
      .goods_deatails(
        `${
          this.$baseurl
        }/bsl_web/project/getProjectDetails?projectLan=zh_CN&projectId=${
          this.details.projectId
        }&signStatus=${this.details.signStatus}&signId=${
          this.details.signId ? this.details.signId : -1
        }`,
        "get",
        {},
        this.details_lists,
        this.nav_lists,
        this.investor_infor
      )
      .then(res => {
        console.log(res);
        // this.investor_infor=[...res.investor_infor]
        // this.nav_lists = [...res.nav_lists];
        // this.details_lists = [...res.details_lists];
        this.title = res.title;
        this.$toast.clear();
      });
  },
  methods: {
    //   cancel() {
    //   this.show2 = false;
    // },
    daship() {
      var newWindow = window.open();
      newWindow.location.href = "http://www.wearetechman.com:5001/webui";
      // tempwindow.location=hash_id;
    },
    // 
    share(val) {
      console.log(val);
      this.message = val;
      this.$copyText(this.message).then(
        e => {
          this.show = true;
        },
        function(e) {
          // alert("Can not copy");
          console.log(e);
        }
      );
    },
    check_contract() {
      this.$loading();
      var newWindow = window.open();
      this.$axios({
        method: "get",
        url: `${this.$baseurl}/bsl_web/projectSign/getPdf?signId=${this.$route.query.signId}`
      }).then(res => {
        this.$toast.clear();
        console.log(res);
        if (res.data.resultCode == 10000) {
          // window.open();
          newWindow.location.href = res.data.data.pdfPath;
        }
      });
    }
  }
};
</script>
<style lang="scss">
#a_wait_investor_comfirm {
  nav {
    // position: relative;
    .van-icon-arrow-left {
      position: absolute;
      left: 0.6rem;
      top: 50%;
      transform: (translate(0, -50%));
    }
  }
  .van-dialog__header {
    font-size: 0.5rem;
  }
  .van-dialog__content {
    margin: 0.5rem 0;
    display: flex;
    justify-content: center;
    div {
      width: 6rem;
      // height: 2rem;
      text-align: center;
      word-wrap: break-word;
    }
    .hash {
      margin-top: 0.2rem;
      color: #0f6ebe;
    }
  }

  .van-dialog {
    font-size: 0.3rem;
  }
  .van-cell {
    font-size: 0.3rem;
    background: #f2f2f2;
    // padding: 0;
    padding: 0.2rem 0.3rem;
    margin: 0 0 0.5rem;
    border: 1px solid #b5b5b5;
  }
}
</style>
<style lang="scss" scoped>
#a_wait_investor_comfirm {
  width: 100%;

  main {
    margin-top: 1.6rem;
    background: #ffffff;
    aside {
      display: flex;
      width: 100%;
      height: 3rem;
      justify-content: center;
    }
    div.investors_infor {
      h2 {
        min-height: 2rem;
        font-size: 0.46rem;
        padding: 0.4rem;
        box-sizing: border-box;
        word-break: break-all;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        color: #0f6ebe;
        font-weight: 600;
        line-height: 0.68rem;
      }
      header {
        height: 0.8rem;
        font-size: 0.32rem;
        text-align: center;
        background: #f2f2f2;
        line-height: 0.8rem;
        color: #868686;
        border-bottom: 0.01rem dashed #b5b5b5;
      }
      ul {
        padding: 0.1rem 0.5rem;
        li {
          > div {
            margin-bottom: 0.1rem;
            display: flex;
            align-items: baseline;
            font-size: 0.38rem;
          }
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 3rem;
          }
          .draft {
            margin-bottom: 0.25rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            line-height: 0.48rem;
            color: #787878;
          }
          .draft1 {
            padding: 0.2rem 0.4rem;
            box-sizing: border-box;
          }
        }
      }
    }
    article {
      margin: 0 0 1.3rem 0;
      header {
        height: 0.8rem;
        font-size: 0.32rem;
        text-align: center;
        // font-weight: 600;
        background: #f2f2f2;
        line-height: 0.8rem;
        color: #868686;
        // border-bottom: 0.01rem dashed #b5b5b5;
      }
      div.nav_lists {
        display: flex;
        border-top: 0;
        border-bottom: 0.2rem solid #f2f2f2;
        > p {
          flex: 1;
          height: 2.5rem;
          font-size: 0.38rem;
          display: flex;
          align-items: center;

          section.box {
            box-sizing: border-box;
            width: 100%;
            display: flex;
            text-align: center;
            height: 2rem;
            //  padding: 0.1rem;
            border-right: 0.08rem solid #f2f2f2;
            flex-direction: column;
            justify-content: space-between;
            span.rowb {
              font-size: 0.6rem;
              color: #0f6ebe;
            }
          }
        }
        p:nth-last-child(1) {
          section.box {
            border-right: 0;
          }
        }
      }
      ul {
        padding: 0.5rem;
        li {
          margin-bottom: 0.1rem;
          display: flex;
          align-items: baseline;
          font-size: 0.38rem;
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 4rem;
          }
          .draft {
            margin-bottom: 0.25rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            line-height: 0.48rem;
            color: #787878;
          }
          .draft1 {
            padding: 0.2rem 0.4rem;
            box-sizing: border-box;
          }
        }
        li.uploadblock {
          img {
            width: 0.6rem;
            vertical-align: middle;
            height: 0.6rem;
          }
        }
      }
      footer {
        padding: 0 0.5rem 0.5rem 0.5rem;
        font-size: 0.38rem;
        text-align: center;
        aside {
          height: 1.8rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          p {
          }
          button {
            height: 1rem;
            color: #ffffff;
          }
          button:nth-of-type(1) {
            background: #00adef;
          }
        }
      }
    }
  }
}
</style>



