<template>
  <div id="a_wait_sendemail">
    <!-- <nav>
      <van-icon name="arrow-left" @click="$global.previous()" />
    </nav>-->
    <commonnav :msg="dad_text"></commonnav>
    <main>
      <div class="investors_infor">
        <h2>{{title}}</h2>
        <header></header>
        <commoninvestors :investor_infor="investor_infor"></commoninvestors>
      </div>
      <article>
        <header></header>
        <boxx :nav_lists="nav_lists"></boxx>
        <commondetails :toson="details_lists"></commondetails>
        <footer>
          <aside>
            <button @click="summit"></button>
          </aside>
        </footer>
        <van-dialog v-model="show2" :show-confirm-button="false">
          <footer class="email">
            <p></p>
            <p class="reminder" >{{show}}</p>
            <div class="emailadress">
              <van-field v-model="emailadress" clearable />
            </div>
            <aside>
              <button @click="submit_email"></button>
              <button @click="cancel"></button>
            </aside>
          </footer>
        </van-dialog>
      </article>
    </main>
    <mbottom></mbottom>
  </div>
</template>
<script>
export default {
  name: "goods_details",
  data() {
    return {
      title: "",
      dad_text: "",
      show2: false, //
      show:'',//
      custmoers_obj: {},
      investorsId: "",
      emailadress: "",
      investor_infor: {
        investorsType: {
          name: ":",
          response: ""
        },
        investorsCompany: {
          name: ":",
          response: ""
        },
        investorsName: {
          name: ":",
          response: ""
        },
        investorsArea: {
          name: ":",
          response: ""
        }
      },
      nav_lists: {
        financingStage: {
          name: "",
          response: ""
        },

        committedCount: {
          name: "</br>",
          response: ""
        },
        interestProjectCount: {
          name: "<br>",
          response: ""
        },
      },
      details_lists: {
        projectIndustry: {
          name: ":",
          response: ""
        },
        projectArea: {
          name: ":",
          response: ""
        },
        // signStatu: {
        //   name: ":",
        //   response: ""
        // },
        projectCompany: {
          name: ":",
          response: ""
        },
        currencyType: {
          name: ":",
          response: ""
        },
        collectMoney: {
          name: ":",
          collectMoneyMin:'',
          collectMoneyMax: "",
        },
        projectMobile: {
          name: ":",
          response: ""
        },
        projectEmail: {
          name: ":",
          response: ""
        },
        projectDescribe: {
          name: ":",
          response: ""
        }
      }
    };
  },
  created() {
    this.$loading();
    this.agent_beforesend();
    let details = this.$route.query;
    this.$global
      .goods_deatails(
        `${
          this.$baseurl
        }/bsl_web/project/getProjectDetails?projectLan=zh_CN&projectId=${
          details.projectId
        }&signStatus=${details.signStatus}&signId=${
          details.signId ? details.signId : -1
        }`,
        "get",
        {},
        this.details_lists,
        this.nav_lists,
        this.investor_infor
      )
      .then(res => {
        console.log(res);
        this.title = res.title;
        this.$toast.clear();
      });
  },
  methods: {
    cancel() {
      this.show2 = false;
           this.show='';
    },
    // 
    summit() {
      this.show2 = true;
    },
    // 
    agent_beforesend() {
      this.$axios({
        method: "get",
        url: `${this.$baseurl}/bsl_web/project/getDetails?signId=${this.$route.query.signId}`
      }).then(res => {
        if (res.data.resultCode == 10000) {
          this.investorsId = res.data.data.investorsId;
          this.custmoers_obj = res.data.data;
          console.log(this.custmoers_obj);
        }
      });
    },
    submit_email() {
       this.show='';
    var reg = /^[A-Za-z0-9]+([_\.][A-Za-z0-9]+)*@([A-Za-z0-9\-]+\.)+[A-Za-z]{2,6}$/;
      if(reg.test(this.emailadress)) {
            this.$loading();
             this.$axios({
        method: "post",
        url: `${this.$baseurl}/bsl_web/projectSign/sendProject4`,
        data: this.$qs.stringify({
          signId: this.$route.query.signId,
          memberEmail: this.emailadress,
          investorsId: this.investorsId,
          emailData: `<html lang="en" style="width: 100%;margin: 0;padding: 0;
            height: 100%;">
                    <head>
                    <meta charset="UTF-8">
                    <meta name="viewport" content="width=device-width, initial-scale=1.0">
                    <meta http-equiv="X-UA-Compatible" content="ie=edge">
                    <title>mobile</title>
                  </head>




          <body>
              <table id="box" style="width: 580px;height:350px;
              margin: auto;
              border-collapse:collapse; border-spacing:0px 10px;
              border:1px solid #cccccc;border-radius:5px;

            ">
                  <thead>
                      <tr>
                          <th colspan="2" height="100">
                              <img style="width: 100px;height: 50px;" src="${this.custmoers_obj.picUrl}"
                                  alt="">
                          </th>
                      </tr>
                  </thead>
                  <tobody>
                      <tr class="column" style="">
                          <td style="text-align:center;vertical-align:top;"></td>
                          <td style="padding:0;width: 430px;text-align:left;vertical-align:top;">
                          </td>
                      </tr>
                      <tr class="column" style="">

                          <td style="text-align:center;vertical-align:top;"></td>
                          <td style="text-align:left;vertical-align:top;">
                            ${this.custmoers_obj.projectName}</td>
                      </tr>
                      <tr class="column" style="margin-bottom: 15px;">
                          <td style="width: 120px;text-align:center;vertical-align:top;"></td>
                          <td style="width: 430px;text-align:left;vertical-align:top;">
                              ${this.custmoers_obj.bslName4}</td>
                      </tr>
                      <tr class="column" style="margin-bottom: 15px;">
                          <td style="width: 120px;text-align:center;vertical-align:top;"></td>
                          <td style="width: 400px;text-align:left;vertical-align:top;">
                              ${this.custmoers_obj.bslName1}
                          </td>
                      </tr>

                      <tr class="column" style="">
                          <td colspan="2" style="text-align:center;vertical-align:center;">
                              <a href="${this.$baseurl3}/#/i_emailto_confirm?projectLan=${this.custmoers_obj.projectLan}&signId=${this.custmoers_obj.signId}" class="button" style="text-decoration:none;">
                                  <span
                                      style="display:inline-block;text-decoration:none;width:200px;height:40px;background: #00B1F5;color:white;line-height:40px;">
                                      
                                  </span>
                              </a>
                          </td>


                      </tr>
                  </tobody>

              </table>
          </body>

          </html>`
        })
      }).then(res => {
        this.$toast.clear();
        console.log(res);
        if (res.data.resultCode == 10000) {
          this.show2 = false;
          this.$dialog
            .alert({
              title: ""
              // message: ""
            })
            .then(() => {
              this.$routerto("a_wait_investor_comfirm", {
                signId: this.$route.query.signId,
                projectId: this.$route.query.projectId,
                signStatus: 6
              });
            });
        } else if (res.data.resultCode == 10010) {
          this.$dialog
            .alert({
              title: ""
            })
            .then(() => {});
        } else if (res.data.resultCode == 10002) {
          this.$dialog
            .alert({
              title: ""
            })
            .then(() => {});
        }
      });
      }else{
          this.show='';
      }


    }
  }
};
</script>
<style lang="scss">
#a_wait_sendemail {
  nav {
    // position: relative;
    .van-icon-arrow-left {
      position: absolute;
      left: 0.6rem;
      top: 50%;
      transform: (translate(0, -50%));
    }
  }
  .van-dialog {
    font-size: 0.4rem;
  }
  .van-cell {
    font-size: 0.3rem;
    background: #f2f2f2;
    // padding: 0;
    padding: 0.2rem 0.3rem;
    margin: 0 0 0.5rem;
    border: 1px solid #b5b5b5;
  }
}
</style>
<style lang="scss" scoped>
#a_wait_sendemail {
  width: 100%;

  main {
    margin-top: 1.6rem;
    background: #ffffff;
    aside {
      display: flex;
      width: 100%;
      height: 3rem;
      justify-content: center;
    }
    div.investors_infor {
      h2 {
        min-height: 2rem;
        font-size: 0.46rem;
        padding: 0.4rem;
        box-sizing: border-box;
        word-break: break-all;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        color: #0f6ebe;
        font-weight: 600;
        line-height: 0.68rem;
      }
      header {
        height: 0.8rem;
        font-size: 0.32rem;
        text-align: center;
        background: #f2f2f2;
        line-height: 0.8rem;
        color: #868686;
        border-bottom: 0.01rem dashed #b5b5b5;
      }
      ul {
        padding: 0.1rem 0.5rem;
        li {
          > div {
            margin-bottom: 0.1rem;
            display: flex;
            align-items: baseline;
            font-size: 0.38rem;
          }
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 3rem;
          }
          .draft {
            margin-bottom: 0.25rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            line-height: 0.48rem;
            color: #787878;
          }
          .draft1 {
            padding: 0.2rem 0.4rem;
            box-sizing: border-box;
          }
        }
      }
    }
    article {
      margin: 0 0 1.3rem 0;
      header {
        height: 0.8rem;
        font-size: 0.32rem;
        text-align: center;
        // font-weight: 600;
        background: #f2f2f2;
        line-height: 0.8rem;
        color: #868686;
        border-bottom: 0.01rem dashed #b5b5b5;
      }
      div.nav_lists {
        display: flex;
        border-top: 0;
        border-bottom: 0.2rem solid #f2f2f2;
        > p {
          flex: 1;
          height: 2.5rem;
          font-size: 0.38rem;
          display: flex;
          align-items: center;

          section.box {
            box-sizing: border-box;
            width: 100%;
            display: flex;
            text-align: center;
            height: 2rem;
            //  padding: 0.1rem;
            border-right: 0.08rem solid #f2f2f2;
            flex-direction: column;
            justify-content: space-between;
            span.rowb {
              font-size: 0.6rem;
              color: #0f6ebe;
            }
          }
        }
        p:nth-last-child(1) {
          section.box {
            border-right: 0;
          }
        }
      }
      ul {
        padding: 0.5rem;
        li {
          margin-bottom: 0.1rem;
          display: flex;
          align-items: baseline;
          font-size: 0.38rem;
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 4rem;
          }
          .draft {
            margin-bottom: 0.25rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            line-height: 0.48rem;
            color: #787878;
          }
          .draft1 {
            padding: 0.2rem 0.4rem;
            box-sizing: border-box;
          }
        }
      }
      footer {
        padding: 0 0.5rem 0.5rem 0.5rem;
        font-size: 0.38rem;
        aside {
          height: 2rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          button {
            height: 1rem;
            color: #ffffff;
          }
          button:nth-of-type(1) {
            background: #00adef;
          }
          button:nth-of-type(2) {
            background: #ff7c2c;
          }
        }
      }
      footer.email {
        padding: 0 0.6rem 0.5rem 0.6rem;
        p {
          text-align: center;
          height: 1rem;
          line-height: 1rem;
          font-size: 0.4rem;
          margin-top: 0.5rem;
        }
        p.reminder {
          margin: 0;
          height: 1rem;
          font-size: 0.38rem;
          line-height: 1rem;
          color: #f36c69;
        }
        aside {
          height: 2.2rem;
          display: flex;
          font-size: 0.3rem;
          flex-direction: column;
          justify-content: space-between;
          button {
            height: 1rem;
            color: #ffffff;
          }
          button:nth-of-type(1) {
            background: #00adef;
          }
          button:nth-of-type(2) {
            background: #ff7c2c;
          }
        }
      }
    }
  }
}
</style>



