<template>
  <div id="i_inverstor_infor">
    <nav>
      <van-icon name="arrow-left" @click="$global.previous()" />
    </nav>
    <main>
      <!-- <aside>
        <img src="../../assets/26566ffb301dac8c24d21969b538612.png" alt />
      </aside>-->
      <article>
        <!-- <header></header> -->
        <ul>
          <li i v-for="(item) in details_lists" :key="item.name">
            <div
              v-if="item.keyword!='investorsCompany' || (item.keyword=='investorsCompany' && item.response!='')"
            >
              <p class="row1">{{item.name}}</p>
              <p class="row2">{{item.response}}</p>
            </div>
          </li>
        </ul>
      </article>
    </main>
    <mbottom></mbottom>
  </div>
</template>
<script>
export default {
  name: "i_inverstor_infor",
  data() {
    return {
      details_lists: [
        {
          keyword: "investorsCompany",
          name: ":",
          response: ""
        },
        {
          keyword: "investorsName",
          name: ":",
          response: ""
        },
        {
          keyword: "investorsArea",
          name: ":",
          response: ""
        },
        {
          keyword: "investorsMobile",
          name: ":",
          response: ""
        },

        {
          keyword: "investorsEmail",
          name: ":",
          response: ""
        },
        {
          keyword: "interestedIndustries",
          name: ":",
          response: ""
        },
        {
          keyword: "investorsCompanyAddress",
          name: ":",
          response: ""
        }
        // {
        //   name: ":",
        //   response: ""
        // }
      ]
    };
  },
  created() {
    this.$loading();
    this.$axios({
      method: "post",
      url: `${this.$baseurl}/bsl_web/projectSign/getInvestorsDetail?investorsId=${this.$route.query.investorsId}`
    }).then(res => {
      for (var i in res.data.data) {
        for (var j = 0; j < this.details_lists.length; j++) {
          if (this.details_lists[j].keyword == i) {
            this.details_lists[j].response = res.data.data[i];
          }
        }
      }
       this.$toast.clear();
    });

  }
};
</script>
<style lang="scss">
#i_inverstor_infor {
  nav {
    position: relative;
    .van-icon-arrow-left {
      // line-height: 1rem;
      position: absolute;
      left: 0.6rem;
      top: 50%;
      transform: (translate(0, -50%));
    }
  }
}
</style>
<style lang="scss" scoped>
#i_inverstor_infor {
  width: 100%;
  nav {
    width: 100%;
    text-align: center;
    line-height: 1.5rem;
    height: 1.5rem;
    position: fixed;
    top: 0;
    font-size: 0.46rem;
    background: white;
    border-bottom: 0.1rem solid #b5b5b5;
  }
  main {
    margin-top: 1.5rem;
    background: #ffffff;
    aside {
      display: flex;
      width: 100%;
      height: 3rem;
      justify-content: center;
    }
    article {
      margin: 0 0 1.3rem 0;
      ul {
        padding: 0.5rem 0.5rem 0 0.5rem;
        li {
          > div {
            margin-bottom: 0.5rem;  
            align-items: baseline;
           font-size: 0.44rem;
            line-height: 0.56rem;
          }
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            margin-bottom: 0.3rem;
          }
          .row2 {
            word-break: break-all;
            line-height: 0.6rem;
            color: #787878;
          }
        }
      }
    }
  }
}
</style>



